<template>
  <div id="alertUpload" v-loading.fullscreen.lock="loading" element-loading-text="">
    <el-dialog title="BP" :visible="dialogUploadVisible" :before-close="handleClose" :show-close="showList">
      <div style="height:250px;"></div><!---->
        <el-upload class="uploadProjec"
                   :action="uploadAddress"
                   :on-preview="handlePreview"
                   :on-change="handleChange"
                   :on-success="uploadsuccess"
                   :on-error="uploaderror"
                   :file-list="fileList"
                   :before-upload="beforeUpload"
                   :on-progress="handleProgress"
                   :data="uploadDate"
                   :show-file-list="showList"_
                   ref="upload"
                   accept=".doc, .ppt, .pdf, .zip, .rar, .docx, .pptx"
                   drag multiple>
          <i class="el-icon-upload"></i>
          <div class="el-upload__text"><!----><em></em></div>
          <div class="el-upload__tip" slot="tip" >BP<br>pdfpptpptxdocdocxziprar</div>
        </el-upload>

      <div slot="footer" class="dialog-footer" style="padding-top: 40px;padding-right: 44px;padding-bottom: 15px;">
        <el-button @click="handleClose"> </el-button>
      </div>
    </el-dialog>


    <el-dialog title="BP" :visible="dialogUpload2Visible" :before-close="handleClose" :show-close="showList">
      <div class="loadmodel">
        <el-upload
          class="upload-demo"
          ref="upload"
          size="large"
          :action="uploadAddress"
          :on-preview="handlePreview"
          :on-change="handleChange"
          :on-success="uploadsuccess"
          :on-progress="handleProgress"
          :on-error="uploaderror"
          :file-list="fileList"
          :data="uploadDate"
          :before-upload="beforeUpload"
          :show-file-list="showList"
          accept=".doc, .ppt, .pdf, .zip, .rar, .docx, .pptx"
          multiple>
          <div class="inner">
            <el-button slot="trigger" type="primary" class="fl"><i class="el-icon-plus"></i></el-button>
            <p class="bp fl">BP<br>pdfpptpptxdocdocxziprar</p>
          </div>

        </el-upload>
        <div class="uploadForm" style="padding-top: 32px;">
          <el-form :model="dateForm" ref="dateForm" label-width="100px" class="demo-dynamic" label-position="top">
            <el-row :span="24" :gutter="10">
              <el-col :span="6">
                <el-form-item
                  label=""
                  :prop="'domains.' + index + '.file_title'"
                  v-for="(domain, index) in dateForm.domains"
                  :key="domain.index"
                  :rules="[{required: true, message: '', trigger: 'blur'}]">
                  <el-input v-model="domain.pro_name"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item
                  label=""
                  :prop="'domains.' + index + '.pro_intro'"
                  v-for="(domain, index) in dateForm.domains"
                  :key="domain.index"
                  :rules="[{required: true, message: '', trigger: 'blur'}]">
                  <el-input v-model="domain.pro_intro"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="10">
                <el-form-item
                  label=""
                  :prop="'domains.' + index + '.pro_name'"
                  v-for="(domain, index) in dateForm.domains"
                  :key="domain.index">
                  <el-input v-model="domain.file_title" :disabled="true"></el-input>
                </el-form-item>

              </el-col>
              <el-col :span="2">
                <el-form-item
                  v-for="(domain, index) in dateForm.domains"
                  :key="domain.index"
                  style="padding-top: 23px;">
                  <el-button v-show="!domain.load" @click.prevent="removeDomain(domain)"></el-button>
                  <span v-show="domain.load" class="uploadImg"><img src="../../../assets/images/loading.gif"></span>
                </el-form-item>
              </el-col>
            </el-row>
          </el-form>
        </div>
        <!--<p class="alertIn">{{alentTitle}}</p>-->
        <div slot="footer" class="dialog-footer clearfix" style="padding-top: 40px;">
          <div class="fr">
            <el-button type="primary" @click="submitUpload('dateForm',dateForm)" :disabled="submitButton"> </el-button>
            <el-button @click="cancel"> </el-button>
          </div>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  props: ["dialogUploadVisible"],
  data () {
    return {
      uploadAddress:this.URL.weitianshiLine+"api/v/project/projectUpload",//
      num:0,//
//      dialogUploadVisible: false,//
      dialogUpload2Visible:false,//
      status:"",//success/exception
      percentage:0,//
      formLabelWidth: '880px',
      fileList:[
      ],//
      dateForm: {//
        domains: [
          /*{
           pro_intro: "",
           pro_name: 2476,
           file_title: "2.docx",
           project_id: 4,
           load:true,
           uid:1501837722250
           }*/
        ],
      },
      loading:false,
      uploadDate:{user_id: localStorage.user_id},//
      loadingcheck:false,
      showList:false,
      alentTitle:"",
      submitButton:false,//false/true
    }
  },
  methods: {
    //1,
    handleChange(file, fileList) {
      this.$emit('changeupload',false);
      this.dialogUpload2Visible=true;
      this.subButtonCheck(this.dateForm.domains);
    },
    uploadsuccess(response, file, fileList){
        let data=response.data;
        if(response.status_code==2000000) {
          this.$tool.success("");
          this.deleteLoad(file.uid);
          this.addDomain(data.pro_intro,data.pro_name,data.file_title,data.project_id,false,file.uid);
          this.subButtonCheck(this.dateForm.domains);
        }
    },
    uploaderror(err, file, fileList){
      this.$tool.error("");
    },
    handlePreview(file) {
      this.$tool.console(file);
    },
    beforeUpload(file){
      this.num++;
      let filetypes=[".doc",".docx",".ppt",".pptx",".pdf",".zip",".rar"];
      let name=file.name;
      let fileend=name.substring(name.lastIndexOf(".")).toLowerCase();
      let isnext = false;
      if(filetypes && filetypes.length>0){
        for(var i =0; i<filetypes.length;i++){
          if(filetypes[i]==fileend){
            isnext = true;
            break;
          }
        }
      }
      this.loading=false;
      if(!isnext){
        setTimeout(()=>{this.$tool.error(file.name+"");},200);
        this.num=0;
        return false;
      }
      if(parseInt(file.size) > parseInt(20971521)){
        setTimeout(()=>{this.$tool.error(file.name+"20m");},200);
        this.num=0;
        return false;
      }
      this.addDomain(file.name, file.name, file.name,0,true,file.uid);
    },
    deleteLoad(uid){
      let lists=this.dateForm.domains;//
      for(let i=0; i<lists.length; i++){
        if(lists[i].uid==uid){
          lists.splice(i,1)
        }
      }
    },//Load
    subButtonCheck(arr){
      for(let i=0; i<arr.length; i++){
        if(arr[i].load){
          this.submitButton=true;
          return false;
        }else{
          this.submitButton=false;
        }
      }
    },//,
    handleProgress(event, file, fileList){
      this.percentage=parseInt(event.percent);
    },
    //
    submitUpload(formName,formData) {
      let obj = this.dateForm.domains;
        this.$refs[formName].validate((valid) => {
        if (valid) {
          const saveUploadURL=this.URL.saveUpload
          this.$http.post(saveUploadURL,obj)
            .then(res=>{
              this.$tool.console(res)
              if(res.status===200){
                this.dialogUpload2Visible=false;
                this.$emit('reload', true);
                this.dateForm.domains=[];
                this.fileList=[];
              }
            }).catch(err=>{
            this.$tool.console(err)
//            this.closeLoading();
          })
          this.$refs.upload.submit();
        } else {
          this.$tool.console('error submit!!');
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
    //
    removeDomain(item) {
      const deleteUpload=this.URL.deleteUpload
      var index = this.dateForm.domains.indexOf(item)
      if (index !== -1) {
        this.dateForm.domains.splice(index, 1)
        this.fileList.splice(index, 1)
      }

      this.$http.post(deleteUpload,{user_id: localStorage.user_id,project_id:item.project_id})
        .then(res=>{
          if(res.status===200){
            this.loading=false;
            this.$tool.success("");
          }
            this.$tool.console(res)
        })
        .catch(err=>{
          this.$tool.console(err)

        })
    },
    //,
    addDomain(pro_intro,pro_name,file_title,project_id,load,uid) {
      let object ={};
      object.pro_intro=pro_intro;
      object.pro_name=pro_name;
      object.file_title=file_title;
      object.project_id=project_id;
      object.load=load;
      object.uid=uid;
      this.dateForm.domains.push(object);
    },
    //,
    cancel(){
      this.$confirm('?')
        .then(_ => {

          let arr=this.dateForm.domains
          this.$tool.console(this.dateForm.domains);
          for(let i=0; i<arr.length; i++){
            this.$http.post(this.URL.deleteUpload,{user_id: localStorage.user_id,project_id:arr[i].project_id})
            .then(res=>{
              if(res.status===200){
              this.loading=false;
              this.$tool.success("");
            }
              this.$tool.console(res)
            })
            .catch(err=>{
              this.$tool.console(err)
            })
          }
          this.$emit('changeupload',false);
          this.dialogUpload2Visible=false;
          this.dateForm.domains=[];
          this.fileList=[];
          conosle.log("")
        })
        .catch(_ => {

        });
    },
//    dialogVisiblechange() {
//      this.$emit('changeupload',false)
//    },
    handleClose(done) {
      this.$confirm('')
        .then(_ => {
          this.$emit('changeupload',false);
          this.dialogUpload2Visible=false;
          done()
        })
        .catch(_ => {});
    },
  }
}
</script>

<style scoped lang="less">
  .alertIn{
    color: red;
  }
  .el-notification{
    z-index: 2005;
  }
  .el-upload__tip{
    text-align: center;
  }
.uploadProjec{
  width: 360px;
  height: 180px;
  position: absolute;
  right: 0px;
  top:0px;
  left: 0px;
  bottom: 0px;
  margin: auto;
  .el-upload-dragger{
    background-color:#f9fafc!important;
    font-size: 40px;
  }
}
.bp{
  display: inline-block;
  font-size:12px;
  color:#5e6d82;
  letter-spacing:0;
  padding-left: 10px;
}
  .el-progress--circle{
    left: 30%;
  }
  .loadmodel{
    width: 100%;
  }
  .el-notification{
    z-index: 2004;
  }
  .uploadImg{
    width: 15px;
    height: 15px;
    display: inline-block;
    img{
      width: 100%;
    }
  }
</style>



