<template>
  <div id="myproject" v-loading.fullscreen.lock="loading" element-loading-text="">
    <div class="wrap-left fl" style="height: 888px;">
      <div class="top-big-progress">
        <div class="pp-item pp-node pp-start" :class="{'pp-cur':node0}" @click="setNode('0')">
          <p class="pp-num pp-txt">{{nodeCount.whole}}</p>
          <span class="pp-sec-title"></span>
        </div>
        <div class="pp-item pp-lines">
          <span class="lp-line">&nbsp;</span>
        </div>
        <div class="pp-item pp-node" :class="{'pp-cur':node1}" @click="setNode('1')">
          <p class="pp-num pp-txt">{{nodeCount.clue}}</p>
          <span class="pp-sec-title"></span>
        </div>
        <div class="pp-item pp-lines">
          <span class="lp-line">&nbsp;</span>
          <el-tooltip placement="top" :class="{'pp-cur':node2}" >
            <div slot="content">
              <div style="width:50px;text-align: center;"> : {{nodeCount.interview}} </div>
            </div>

              <span class="circle circle-0" @click="setNode('2')">
                <span class="getClick"></span>
            </span>
          </el-tooltip>
          <el-tooltip placement="top" :class="{'pp-cur':node3}" >
            <div slot="content">
              <div style="width:50px;text-align: center;"> : {{nodeCount.investigate}} </div>
            </div>
            <span class="circle circle-5" @click="setNode('3')">
              <span class="getClick"></span>
            </span>
          </el-tooltip>
        </div>
        <div class="pp-item pp-node" :class="{'pp-cur':node4}" @click="setNode('4')">
          <p class="pp-num pp-txt">{{nodeCount.sign}}</p>
          <span class="pp-sec-title">FA</span>
        </div>
        <div class="pp-item pp-lines">
          <span class="lp-line">&nbsp;</span>
          <el-tooltip placement="top" :class="{'pp-cur':node5}" >
            <div slot="content">
              <div style="width:100px;text-align: center;"> : {{nodeCount.recommended}} </div>
            </div>
            <span class="circle circle-1" @click="setNode('5')">
              <span class="getClick"></span>
            </span>
          </el-tooltip>

          <el-tooltip placement="top" :class="{'pp-cur':node6}" >
            <div slot="content">
              <div style="width:80px;text-align: center;"> : {{nodeCount.agreement}} </div>
            </div>
            <span class="circle circle-2" @click="setNode('6')">
              <span class="getClick"></span>
            </span>
          </el-tooltip>

          <el-tooltip placement="top" :class="{'pp-cur':node7}" >
            <div slot="content">
              <div style="width:50px;text-align: center;"> : {{nodeCount.delivery}} </div>
            </div>
            <span class="circle circle-3" @click="setNode('7')">
              <span class="getClick"></span>
            </span>
          </el-tooltip>

          <el-tooltip placement="top" :class="{'pp-cur':node8}">
            <div slot="content">
              <div style="width:80px;text-align: center;"> : {{nodeCount.collect}} </div>
            </div>
            <span class="circle circle-4"  @click="setNode('8')">
              <span class="getClick"></span>
            </span>
          </el-tooltip>

        </div>
        <div class="pp-item pp-node" :class="{'pp-cur':node9}" @click="setNode('9')">
          <p class="pp-num pp-txt">{{nodeCount.revenue}}</p>
          <span class="pp-sec-title"  ></span>
        </div>
      </div>
      <div class="clearfx"></div>
      <div class="top-search-box">
        <div class="input-box">
          <el-input
            placeholder=""
            icon="search"
            v-model="searchinput"
            :on-icon-click="handleIconClick"
            @keyup.native.enter="handleIconClick">
          </el-input>
        </div>
        <div class="btns-box">
          <el-button type="primary" @click="dialogUploadVisible = true"></el-button>
          <el-button type="primary" @click="createProject"></el-button>

          <alertUpload :dialog-upload-visible="dialogUploadVisible" v-on:changeupload="dialogUploadVisiblechange" @reload="handleIconClick">

          </alertUpload>
        </div>
      </div>
      <div class="top-lists" style="cursor: pointer">
          <template>
            <el-table :data="tableData" style="width: 100%"
                      @row-click="handleSelect"
                      @header-click="headerClick"
                      @sort-change="filterChange"
                      @filter-change="filterChange" stripe>
            <el-table-column prop="pro_name" label="" width="144" show-overflow-tooltip>
              <template scope="scope">
                <el-tooltip placement="top" :disabled="scope.row.pro_name.length > 7 ? false:true">
                  <div slot="content">
                    <div class="tips-txt">{{scope.row.pro_name}}</div>
                  </div>
                  <div>
                    {{scope.row.pro_name}}
                  </div>
                </el-tooltip>
                <div v-if="scope.row.pro_name.length === 0">
                  -
                </div>
              </template>
            </el-table-column>

            <el-table-column prop="pro_intro" label="" width="174" show-overflow-tooltip>
              <template scope="scope">
                <el-tooltip placement="top" :disabled="scope.row.pro_intro.length > 10 ? false:true">
                  <div slot="content">
                    <div class="tips-txt">{{scope.row.pro_intro}}</div>
                  </div>
                  <div>
                    {{scope.row.pro_intro}}
                  </div>
                </el-tooltip>
              </template>
            </el-table-column>

            <el-table-column prop="pro_company_name" label="" width="96" show-overflow-tooltip>
              <template scope="scope">
                <el-tooltip placement="top" :disabled="scope.row.pro_company_name.length > 5 ? false:true">
                  <div slot="content">
                    <div class="tips-txt">{{scope.row.pro_company_name}}</div>
                  </div>
                  <div>
                    {{scope.row.pro_company_name}}
                  </div>
                </el-tooltip>
                <div v-if="scope.row.pro_company_name.length === 0">
                  -
                </div>
              </template>
            </el-table-column>

            <el-table-column prop="pro_source" label="" width="96" :filters="pro_sourceFilters"
                             filter-placement="bottom-end"
                             column-key="pro_source"
                             show-overflow-tooltip	>
              <template scope="scope">
                <div v-if="scope.row.pro_source.length === 0">
                  -
                </div>
                <div v-if="scope.row.pro_source.length > 0">
                  {{scope.row.pro_source}}
                </div>
              </template>
            </el-table-column>

            <el-table-column prop="pro_follow_up_user" label="" width="96" show-overflow-tooltip>
            </el-table-column>

            <el-table-column prop="pro_schedule" label="" width="96" :filters="pro_scheduleFilters"
                             filter-placement="bottom-end"
                             :filter-multiple="stateCheck"
                             column-key="pro_schedule"
                             sortable="custom">
              <template scope="scope">
                <div v-if="scope.row.pro_schedule==''">
                  -
                </div>
                <div else>
                  {{scope.row.pro_schedule}}
                </div>
              </template>
            </el-table-column>

            <el-table-column prop="pro_industry" label="" width="144"
                             :filters="pro_industryFilters"
                             filter-placement="bottom-end"
                             show-overflow-tooltip
                             column-key="pro_industry">
              <template scope="scope">
                <el-tooltip placement="top" :disabled="scope.row.pro_industry.length > 8 ? false:true">
                  <div slot="content">
                    <div class="tips-txt">{{scope.row.pro_industry}}</div>
                  </div>
                  <div>
                    {{scope.row.pro_industry}}
                  </div>
                </el-tooltip>
                <div v-if="scope.row.pro_industry==''">
                  -
                </div>
              </template>
            </el-table-column>

            <el-table-column prop="is_exclusive" label="" width="98" :filters="soleFilters"
                             filter-placement="bottom-end" sortable="custom" column-key="is_exclusive">
              <template scope="scope">

                <el-tag :type="scope.row.is_exclusive === '' ? 'primary' : scope.row.is_exclusive === ''  ? 'success':'gray' " close-transition>
                     <div v-if="scope.row.is_exclusive.length === 0">
                       -
                     </div>
                     <div else>
                       {{scope.row.is_exclusive}}
                     </div>
                </el-tag>
              </template>
            </el-table-column>

            <el-table-column prop="pro_stage" label="" width="80" :filters="pro_stageFilters"
                             filter-placement="bottom-end"
                             sortable="custom" column-key="pro_stage">
              <template scope="scope">
                <div v-if="scope.row.pro_stage.length === 0">
                  -
                </div>
                <div v-if="scope.row.pro_stage.length > 0">
                  {{scope.row.pro_stage}}
                </div>
              </template>
            </el-table-column>
            <el-table-column prop="pro_area" label="" width="80"
                             column-key="pro_area"
                             :filters="pro_areaFilters"
                             filter-placement="bottom-end"
                             sortable="custom">
              <template scope="scope">
                <div v-if="scope.row.pro_area.length === 0">
                  -
                </div>
                <div v-if="scope.row.pro_area.length > 0">
                  {{scope.row.pro_area}}
                </div>
              </template>
            </el-table-column>
            <el-table-column prop="pro_scale" label="" width="102"
                             :filters="pro_scaleFilters"
                             filter-placement="bottom-end"
                             column-key="pro_scale"
                             :filter-multiple="stateCheck">
              <template scope="scope">
                <div v-if="scope.row.pro_scale.length === 0">
                  -
                </div>
                <div v-if="scope.row.pro_scale.length > 0">
                  {{scope.row.pro_scale}}
                </div>
              </template>
            </el-table-column>
            <el-table-column
              prop="reset"
              label=""
              width="130" class="set-th">
              <template scope="scope">
                <el-button
                  @click="handleEdit(scope.$index, scope.row)"
                  type="text"
                  size="small" class="edits-btn btn-cur">
                  
                </el-button>
                <el-button
                  @click="addFollow(scope.$index, scope.row)"
                  type="text"
                  size="small" class="flow-btn btn-cur">
                  
                </el-button>
                <el-button
                  @click="addprojectPush(scope.$index, scope.row)"
                  type="text"
                  size="small" class="send-btn btn-cur">
                  
                </el-button>
                <el-button
                  type="text"
                  size="small" class="send-btn btn-cur">
                  <img src="../../../assets/images/more.png" alt="" class="more">
                </el-button>
                <el-select v-model="scope.row.moreShow" placeholder="" @change="moreChange(scope.$index, scope.row)">
                  <el-option
                    v-for="item in moreList"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </template>
            </el-table-column>
          </el-table>
          <div class="pagenav" v-if="totalData>10">
            <el-pagination
              small
              @current-change="filterChangeCurrent"
              :current-page.sync="currentPage"
              :page-size="10"
              layout="total, prev, pager, next"
              :total="totalData">
            </el-pagination>
          </div>
        </template>
      </div>
    </div>

    <!---->
    <addfollow :dialog-follow="dialogFollow" :projectid="projecmessage.project_id" :projectname="projecmessage.project_name" @changeClose="closeFollow"></addfollow>

    <!---->
    <projectpush2 :dialog-push="dialogPushVisible" :proid="pushId" :proName="pushName" :emitPush="emitPush" @changeClose="dialogVisiblechangeCloase" @preview="dialogPrechange"></projectpush2>

    <!---->
    <projectpreview :dialog-preview-visible="dialogPreviewVisible" :comeFrom="'project'" @changeCon="dialogPreviewVisible=false;" @closePreview="closePreview" @previewPush="previewPush"></projectpreview>

<!--    <div class="page-grid wrap-right contain-right-2 fl">
      <div class="main-box">
        <div class="title-box">
          <span class="lit-line"></span>
          <span class="title"></span>
          <span class="lit-line"></span>
        </div>
      </div>
    </div>-->

  </div>
</template>

<script type="text/ecmascript-6">
  import ElButton from "../../../../node_modules/element-ui/packages/button/src/button";
  import alertUpload from './alertUpload.vue'
  import addfollow from './../followUp/addFollow.vue'
  import projectpush2 from './projectPush2.vue'
  import projectpreview from '../myContacts/projectPreview.vue'
  export default {
    components: {
      ElButton,
      alertUpload,
      addfollow,
      projectpush2,
      projectpreview
    },
    data() {
      return {
        dialogFollow:false,//
        dialogPushVisible:false,//
        projecmessage:{
          project_id:'',
          project_name:''
        },//,ID,
        tableData: [
/*            {
          pro_name: '',
          pro_intro:'',
          pro_company_name: '',
          pro_source: '1',
          pro_follow_up_user: '',
          pro_schedule:'1',
          pro_industry:'',
          is_exclusive:'',
          pro_stage:'IPO',
          pro_area:'',
          pro_scale:'3000',
          project_id:''
        }*/
        ],
        pro_sourceFilters:[
/*            { text: '', value: '' }*/
            ],
        pro_scheduleFilters:[
 /*{ text: '', value: '' }*/
            ],
        pro_industryFilters:[/*{ text: '', value: '' }*/],
        soleFilters:[{ text: '', value:1},{ text: '', value:2},{ text: '', value:0}],
        pro_stageFilters:[/*{ text: 'IPO', value: 'IPO' }*/],
        pro_areaFilters:[],
        pro_scaleFilters:[/*{ text: '3000', value: '3000' }*/],
        stateCheck:false,//
        searchinput:'',//
        dialogUploadVisible: false,//
        node0:true,
        node1:false,
        node2:false,
        node3:false,
        node4:false,
        node5:false,
        node6:false,
        node7:false,
        node8:false,
        node9:false,
        currentPage:1,//
        totalData:0,//
        nodeCount:{
          whole:0,//
          clue:0,//
          interview:0,//
          investigate:0,//
          sign:0,//FA
          recommended:0,//
          agreement:0,//
          delivery:0,//
          collect:0,//
          revenue:0//
        },
        loading:false,
        filter:true,
        getPra:{},//
        getProjectListURL:'',//URL
        moreShow:false,//
        moreList:[{
          value: '0',
          label: ''
        }],//
        pushId:'',//-ID
        pushName:'',//-
        dialogPreviewVisible:false,//
        emitPush:false,//-
      }
    },
    methods:{
      handleSelect(row, event, column) {
        if(column.label!=""){
          this.$router.push({ name: 'projectDetails', query: { project_id:row.project_id,show:'detail'}})
        }
      },//
      handleEdit(index, row){
        this.$router.push({ name: 'editproject', query: { project_id:row.project_id}})
      },//
      createProject(){
        this.$router.push({ name: 'creatproject'})
      },//
      dialogUploadVisiblechange(msg){
        this.dialogUploadVisible=msg;
      },//
      /**/
      addFollow(index, row){
        this.dialogFollow=true;
        this.projecmessage.project_id=row.project_id;
        this.projecmessage.project_name=row.pro_name;
      },//
      closeFollow(msg){
        this.dialogFollow=msg;
      },//
      addprojectPush(index, row){
        this.pushId=row.project_id;
        this.pushName=row.pro_name;
        this.dialogPushVisible=true;
      },//
      dialogVisiblechangeCloase(msg){
        this.dialogPushVisible=msg;
      },//
      /**/
      handleIconClick(){
        this.loading=true;
        this.getPra.user_id=localStorage.user_id;
        this.getPra.search=this.searchinput;
        this.currentPage=1;
        this.getPra.page=1;
        this.$http.post(this.getProjectListURL,this.getPra)
          .then(res=>{
            let data = res.data.data
            this.tableData=this.getProjectList(data)
            this.loading=false;
            this.totalData=res.data.count;
          })
          .catch(err=>{
            this.loading=false;
            this.$tool.console(err,2)
          })
      },//===

      filterChange(filters){
        this.loading=true;
        this.currentPage=1;
        this.getPra.user_id=localStorage.user_id;
        if(filters.pro_schedule) {
            if(parseInt(filters.pro_schedule)){
              this.setNode(parseInt(filters.pro_schedule))
            } else if(filters.pro_schedule.length=== 0) {
              this.setNode(0)
            }
        }//
        if(filters.order){
          if(filters.order=="ascending") filters.order="asc"//
          else filters.order="desc";
          this.getPra.order=filters.prop;
          this.getPra.sort=filters.order;
        }else{
//          let para="";
          for(let key in filters){
            /*             for(let i=0;i<filters[key].length; i++){
             if(key=="pro_source") para+=filters[key][i]+',';
              else*/
                /*para+=parseInt(filters[key][i])+',';
            }*/
//            this.getPra[key]=parseInt(para.substr(0, para.length - 1));
            this.getPra[key]=filters[key];

          }
        } //
        for(let key in this.getPra){
          if(this.getPra[key]=='' || this.getPra[key]=='NaN'){
            delete this.getPra[key];
          }
        }//
        this.$tool.console(this.getPra);
        this.$http.post(this.getProjectListURL,this.getPra)
          .then(res=>{
            this.loading=false;
            let data = res.data.data;
            this.tableData=this.getProjectList(data);
            this.totalData=res.data.count;
          })
          .catch(err=>{
            this.loading=false
            this.$tool.console(err,2)
          })
      },// ascending/descending/
      filterChangeCurrent(page){
        delete this.getPra.page;
        this.loading=true;
        this.getPra.user_id=localStorage.user_id;
        this.getPra.page=page;//
        this.$http.post(this.getProjectListURL,this.getPra)
          .then(res=>{
            this.loading=false;
            let data = res.data.data;
            this.$tool.console(res)
            this.tableData=this.getProjectList(data);
          })
          .catch(err=>{
            this.loading=false
            this.$tool.console(err,2)
          })
      },//
      setNode(v){
        this.currentPage=1;
        this.loading=true  ;
        this.node0 = false ;
        this.node1 = false ;
        this.node2 = false ;
        this.node3 = false ;
        this.node4 = false ;
        this.node5 = false ;
        this.node6 = false ;
        this.node7 = false ;
        this.node8 = false ;
        this.node9 = false ;
        this['node' + v] = true ;
        this.$http.post(this.getProjectListURL,{user_id: localStorage.user_id,pro_schedule:parseInt(v)})
          .then(res=>{
            this.loading=false
            let data = res.data.data
            this.tableData=this.getProjectList(data)
          })
          .catch(err=>{
            this.loading=false
            this.$tool.console(err,2)
          })

      },//
      headerClick(column, event){
        if(column.label===""){
          window.location.reload();
        }
      },//

      getNodeCount(){
        const getNodeCountURL=this.URL.getNodeCount
        this.$http.post(getNodeCountURL,{user_id: localStorage.user_id})
          .then(res=>{
              let data = res.data.data

              this.nodeCount.whole=data.total.schedule_count//
              this.totalData=data.total.schedule_count//
              let nodeList=data.node_list;
              for(let key in nodeList){//
                switch (nodeList[key].schedule_id){
                  case 1:
                    this.nodeCount.clue=nodeList[key].schedule_count
                    break;
                  case 2:
                    this.nodeCount.interview=nodeList[key].schedule_count
                    break;
                  case 3:
                    this.nodeCount.investigate=nodeList[key].schedule_count
                    break;
                  case 4:
                    this.nodeCount.sign=nodeList[key].schedule_count
                    break;
                  case 5:
                    this.nodeCount.recommended=nodeList[key].schedule_count
                    break;
                  case 6:
                    this.nodeCount.agreement=nodeList[key].schedule_count
                    break;
                  case 7:
                    this.nodeCount.delivery=nodeList[key].schedule_count
                    break;
                  case 8:
                    this.nodeCount.collect=nodeList[key].schedule_count
                    break;
                  case 9:
                    this.nodeCount.revenue=nodeList[key].schedule_count
                    break;
                  default:
                    alert("")
                    break;
                };
              }
//              this.loading=false;

          })
          .catch(err=>{

//            this.loading=false;
          })
      },// 
      titleSift(){
        this.loading=true;
        const titleSiftURL=this.URL.titleSift
        this.$http.post(titleSiftURL,{user_id: localStorage.user_id})
          .then(res=>{
            let data = res.data.data
//            this.$tool.console(data)
            let pro_area=data.pro_area;//
            let pro_industry=data.pro_industry;//
            let pro_scale=data.pro_scale;//
            let pro_schedule=data.pro_schedule;//
            let pro_source=data.pro_source;//
            let pro_stage=data.pro_stage;//
            this.pro_areaFilters=this.$tool.getTitleSift(pro_area);
            this.pro_industryFilters=this.$tool.getTitleSift(pro_industry);
            this.pro_scaleFilters=this.$tool.getTitleSift(pro_scale);
            this.pro_scheduleFilters=this.$tool.getTitleSift(pro_schedule);
            this.pro_sourceFilters=this.$tool.getTitleSift(pro_source);
            this.pro_stageFilters=this.$tool.getTitleSift(pro_stage);
            this.loading=false;
          })
          .catch(err=>{
            this.loading=false;
            this.$tool.console(err,2)
          })
      },// 

      getProjectList(list){
        let arr = new Array;
        for(let i=0; i<list.length; i++){
          let obj=new Object;
          obj.pro_name=list[i].pro_name;
          obj.pro_intro=list[i].pro_intro;
          obj.pro_company_name=list[i].pro_company_name;
          obj.pro_source=this.$tool.setTagToString(list[i].pro_source,'tag_name');
          obj.pro_follow_up_user=list[i].pro_follow_up_user;
          obj.pro_schedule=list[i].pro_schedule;
          obj.pro_industry=this.$tool.setTagToString(list[i].pro_industry,'industry_name');
          obj.is_exclusive=list[i].is_exclusive;
          obj.pro_stage=this.$tool.setTagToString(list[i].pro_stage,'stage_name');
          obj.pro_area=this.$tool.setTagToString(list[i].pro_area,'area_title');
          obj.pro_scale=this.$tool.setTagToString(list[i].pro_scale,'scale_money');
          obj.project_id=list[i].project_id;
          obj.moreShow="";
          arr.push(obj);
        }
        return arr
      },//

      /**/
      moreChange(index,row){
        this.moreShow=!this.moreShow;
        if(this.moreShow){
          this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this.loading=true;
            this.$http.post(this.URL.deleteProject, {user_id:localStorage.user_id,project_id: row.project_id})
              .then(res => {
                this.loading=false;
                this.$tool.success("");
                this.handleIconClick();

              })
              .catch(err => {
                this.loading=false;
                this.$tool.error("");
                this.$tool.console(err);
              })

          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
        }


      },//

      previewPush(x){
        this.emitPush=x;
      },//
      dialogPrechange(msg){
        this.dialogPreviewVisible=msg;
      },//
      closePreview(msg){
        this.dialogPreviewVisible=msg;
      },//
    },
    computed: {

    },
    created () {
      // 
      this.getProjectListURL=this.URL.getProjectList
      this.loading=true;
      this.getNodeCount();
      this.titleSift();
      this.handleIconClick();
    },
    watch: {
      // 
      "$route": "handleIconClick"
    }

  }
</script>

<style type="text/css" lang="less">
